<template>
	<div class="sub-container">
		<div class="detail-header">
			<div class="detail-image">
				<img :src="detail.thumbnailImgPath" alt="pre" />
			</div>
			<div class="detail-info">
				<h2 class="marB-30">{{ detail.name }}</h2>
				<ul class="program-detail">
					<li>
						<div class="detail-icon"><Price /></div>
						<span class="body3">{{ detail.price }}</span>
					</li>
				</ul>
				<div class="payment-select">
					<div class="select-box">
						<SelectBox
							v-if="options"
							class="marB-20"
							:options="options"
							v-model="selected"
							placeholder="옵션을 선택해주세요"
						/>
					</div>

					<div class="select-box">
						<SelectBox
							v-if="options"
							class="marB-20"
							:options="countOption"
							v-model="selectedCount"
							placeholder="수량을 선택해주세요"
						/>
					</div>
					<button class="btn">담기</button>
				</div>
			</div>
		</div>
		<section class="detail-contents body3">
			<img :src="detail.detailImgPath" />
		</section>
		<section class="refund-policy clr">
			<h4 class="marB-20">교환·반품 및 환불 정책</h4>

			<div class="policy-content">
				<p class="bold">
					교환·반품 및 환불 정책에 대한 정보 수급이 필요합니다. 임의로 더미
					텍스트 작업하였습니다.
				</p>
				<p class="bold">나. 소비자의 귀책사유로 인한 강습 환불일 경우</p>
				<p class="bold">1) 개시일 이전</p>
				<p>· 행사 7일전: 전액 환불</p>
				<p>· 행사 5~6일전: 70% 환불</p>
				<p>· 행사 3~4일전: 50% 환불</p>
				<p>· 행사 2일전: 환불 불가</p>
			</div>
		</section>
		<LoadingSpinner :loading="isLoading" />
	</div>
</template>

<script>
import Price from '@/assets/icon/program_goods/Price.vue';
import SelectBox from '@/components/common/SelectBox';
import { fetchGoodsDetail } from '@/api/goods';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	created() {
		this.id = this.$route.params.id;
		this.fetchGoodsDetail(this.id);
	},
	data() {
		return {
			id: '',
			detail: {},
			options: [],
			selected: {},
			countOption: [
				{ value: 1, label: '1개' },
				{ value: 2, label: '2개' },
				{ value: 3, label: '3개' },
				{ value: 4, label: '4개' },
			],
			selectedCount: {},
		};
	},
	components: {
		Price,
		SelectBox,
		LoadingSpinner,
	},
	methods: {
		async fetchGoodsDetail(id) {
			this.isLoading = true;
			try {
				const result = await fetchGoodsDetail(id);
				this.detail = result.data;
				this.options = this.detail.options.map(item => {
					item.label = item.colorAndSize;
					return item;
				});
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
};
</script>

<style scoped>
.detail-contents,
.refund-policy {
	width: 700px;
}

.detail-contents img {
	width: 100%;
}

.detail-icon {
	display: inline-block;
	width: 18px;
	margin-right: 8px;
	vertical-align: middle;
}

.option-box {
	position: relative;
}
</style>
